import React, {useState} from 'react';
import {Modal, ModalBody, ModalFooter, ModalTitle, Button} from "react-bootstrap";
import ModalHeader from "react-bootstrap/ModalHeader";

const SectionsRenderer = ({children, sectionName, sectionCount, index, activeIndex, setActiveIndex}) => {
    const goPrev = () => setActiveIndex(activeIndex - 1);
    const goNext = () => setActiveIndex(activeIndex + 1);
//TODO this state needs to be stored in the Form
    const [show, setShow] = useState(true);

    const showBackButton = index > 0;
    const showNextButton = index < sectionCount - 1;
    const showDoneButton = index === sectionCount - 1;

    const handleEnterKeyPress = event => {
        if(event.key === 'Enter') {
            if (showNextButton) {
                goNext();
            } else if (showDoneButton) {
                hideModal();
            }
        }
    }

    const hideModal = event => setShow(false);

    return (
        <Modal
            size={"lg"}
            key={`card-${sectionName}`}
            aria-labelledby="contained-modal-title-vcenter"
            centered
            show={show && sectionName.length && index === activeIndex}
            renderTrigger={() => (
                <h2 className="sectionTrigger" tabIndex="0">
                    {`${sectionName}`}
                </h2>
            )}
            onKeyPress={handleEnterKeyPress}>
            <ModalHeader>
                <ModalTitle id="contained-modal-title-vcenter">
                    {`${sectionName}`}
                </ModalTitle>
            </ModalHeader>
            <ModalBody>
                {children()}
            </ModalBody>
            <ModalFooter>
                {showBackButton && <Button variant="secondary" onClick={goPrev}>&lt; Previous</Button>}
                {showNextButton && <Button variant="primary" onClick={goNext}>Next &gt;</Button>}
                {showDoneButton && <Button variant="primary" onClick={hideModal}>Done</Button>}
            </ModalFooter>
        </Modal>

    );
};

export default SectionsRenderer;
